import style from "../../AdmissionManager/OngoingAdmission/OngoingAdmission.module.css";
import {
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import HostelOngoingAdmissionCardMenu from "./HostelOngoingAdmissionCardMenu";
import { admissionOneTabAccess } from "../../../Constant/Admission/admission_constant";
import { useTranslation } from "react-i18next";
// import HostelCompleteApplicationMenu from "../HostelCompleteApplication/HostelCompleteApplicationMenu";

const HostelOngoingAdmissionCard = ({
  application,
  onRefetch,
  aid,
  isAuthorization,
}) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [completed, setCompleted] = useState(false);
  const navigate = useNavigate();
  const params = useParams();
  const getQuery = useLocation();
  const onMenu = (e) => {
    e.stopPropagation();
    if (authorized[isAuthorization]) setCompleted(true);
    else setAdd(true);
  };
  const onClose = () => {
    setAdd(false);
  };
  const onCloseComplete = () => {
    setCompleted(false);
  };
  const onNavigate = (e, value) => {
    if (e) e.stopPropagation();
    let url = "";
    if (value)
      url =
        getQuery.state?.urlOffSet === "Institute"
          ? `/q/${params.username}/institute/member/admission?type=hostel-ongoing-detail&n=${value}`
          : `/q/${params.username}/member/staff/admission?type=hostel-ongoing-detail&n=${value}`;
    else
      url =
        getQuery.state?.urlOffSet === "Institute"
          ? `/q/${params.username}/institute/member/admission?type=hostel-ongoing-detail`
          : `/q/${params.username}/member/staff/admission?type=hostel-ongoing-detail`;

    navigate(url, {
      state: {
        ...getQuery.state,
        appId: application?._id,
        applicationName: application?.applicationName,
        isAuthorization: isAuthorization,
        applicationData: {
          application: application?.receievedCount,
          document: application?.selectCount,
          fees: application?.fee_collect_count,
          confirmed: application?.confirmCount,
          review: application?.review_count,
          allottment: application?.allotCount,
          cancelled: application?.cancelCount,
        },
      },
    });
  };

  // console.info("application", getQuery.state?.subRole);
  return (
    <>
      <div
        className={style.ongoing_card}
        onClick={onNavigate}
        style={{
          position: "relative",
        }}
      >
        <div className={style.ongoing_card_inner}>
          <img
            alt="Ongoing App"
            src={`${assestsUserAdmissionUrl}/application.svg`}
          />
          <div className={style.ongoing_card_text}>
            <h6>{application?.applicationName ?? ""}</h6>
            <p>{application?.applicationDepartment?.dName ?? ""}</p>
          </div>
        </div>
        <div className={style.ongoing_card_count_date}>
          {getQuery.state?.subRole ? (
            <div className={style.ongoing_card_count_container}>
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Application" && (
                <div
                  className={style.ongoing_card_count_container_inner}
                  onClick={() => onNavigate("")}
                >
                  <p title="Application">{t("application")}</p>
                  <h6 title="Application">
                    {application?.receievedCount ?? 0}
                  </h6>
                </div>
              )}
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Selected" && (
                <div
                  className={style.ongoing_card_count_container_inner}
                  onClick={(e) => onNavigate(e, "document")}
                >
                  <p title="Docs Collect">{t("docs")}</p>
                  <h6 title="Docs Collect">{application?.selectCount ?? 0}</h6>
                </div>
              )}
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "FEE COLLECTION TAB" && (
                <div
                  className={style.ongoing_card_count_container_inner}
                  onClick={(e) => onNavigate(e, "fees")}
                >
                  <p title="Fees Collection">{t("fees_")}</p>
                  <h6 title="Fees Collection">
                    {application?.fee_collect_count ?? 0}
                  </h6>
                </div>
              )}
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Confirmed" && (
                <div
                  className={style.ongoing_card_count_container_inner}
                  onClick={(e) => onNavigate(e, "confirmed")}
                >
                  <p title="Confirmed">{t("confirm")}</p>
                  <h6 title="Confirmed">{application?.confirmCount ?? 0}</h6>
                </div>
              )}
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "REVIEW TAB" && (
                <div
                  className={style.ongoing_card_count_container_inner}
                  onClick={(e) => onNavigate(e, "review")}
                >
                  <p title="Review">{t("review")}</p>
                  <h6 title="Review">{application?.review_count ?? 0}</h6>
                </div>
              )}
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Allotted" && (
                <div
                  className={style.ongoing_card_count_container_inner}
                  onClick={(e) => onNavigate(e, "allottment")}
                >
                  <p title="Allottment">{t("allotted")}</p>
                  <h6 title="Allottment">{application?.allotCount ?? 0}</h6>
                </div>
              )}

              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Cancelled" && (
                <div
                  className={style.ongoing_card_count_container_inner}
                  onClick={(e) => onNavigate(e, "cancelled")}
                >
                  <p title="Cancelled">{t("cancel")}</p>
                  <h6 title="Cancelled">{application?.cancelCount ?? 0}</h6>
                </div>
              )}
            </div>
          ) : (
            <div className={style.ongoing_card_count_container}>
              <div
                className={style.ongoing_card_count_container_inner}
                onClick={() => onNavigate("")}
              >
                <p title="Application">{t("application")}</p>
                <h6 title="Application">{application?.receievedCount ?? 0}</h6>
              </div>

              <div
                className={style.ongoing_card_count_container_inner}
                onClick={(e) => onNavigate(e, "document")}
              >
                <p title="Docs Collect">{t("docs")}</p>
                <h6 title="Docs Collect">{application?.selectCount ?? 0}</h6>
              </div>

              <div
                className={style.ongoing_card_count_container_inner}
                onClick={(e) => onNavigate(e, "fees")}
              >
                <p title="Fees Collection">{t("fees_")}</p>
                <h6 title="Fees Collection">
                  {application?.fee_collect_count ?? 0}
                </h6>
              </div>

              <div
                className={style.ongoing_card_count_container_inner}
                onClick={(e) => onNavigate(e, "confirmed")}
              >
                <p title="Confirmed">{t("confirm")}</p>
                <h6 title="Confirmed">{application?.confirmCount ?? 0}</h6>
              </div>

              <div
                className={style.ongoing_card_count_container_inner}
                onClick={(e) => onNavigate(e, "review")}
              >
                <p title="Review">{t("review")}</p>
                <h6 title="Review">{application?.review_count ?? 0}</h6>
              </div>

              <div
                className={style.ongoing_card_count_container_inner}
                onClick={(e) => onNavigate(e, "allottment")}
              >
                <p title="Allottment">{t("allotted")}</p>
                <h6 title="Allottment">{application?.allotCount ?? 0}</h6>
              </div>

              <div
                className={style.ongoing_card_count_container_inner}
                onClick={(e) => onNavigate(e, "cancelled")}
              >
                <p title="Cancelled">{t("cancel")}</p>
                <h6 title="Cancelled">{application?.cancelCount ?? 0}</h6>
              </div>
            </div>
          )}
        </div>
        {/* {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : ( */}
        {getQuery.state?.role ? null : (
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onMenu}
            className={style.menu_icon}
            title="Menu"
          />
        )}
      </div>
      {add && (
        <HostelOngoingAdmissionCardMenu
          onClose={onClose}
          aid={aid}
          appId={application?._id}
          onRefetch={onRefetch}
          formId={application?.student_form_setting}
          menuData={application}
        />
      )}
      {/* {completed && (
        <HostelCompleteApplicationMenu
          onClose={onCloseComplete}
          appId={application?._id}
          onRefetch={onRefetch}
        />
      )} */}
    </>
  );
};

export default HostelOngoingAdmissionCard;

const authorized = {
  isCompleted: true,
};
