import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import styleIncome from "../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback, useEffect, useState } from "react";
import { useAdmissionCreateApplication } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import FormInput from "../../../Ui/Input/FormInput";
import HostelCreateApplicationOtherInfo from "./HostelCreateApplicationOtherInfo";
import Modal from "../../../Ui/Modal/Modal";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CustomSelectUnit from "../../../Ui/Select/CustomSelectUnit";
import CustomSelectBatch from "../../../Ui/Select/CustomSelectBatch";
import CustomSelectStandard from "../../../Ui/Select/CustomSelectStandard";
import {
  useHostelBatchList,
  useHostelStandardList,
  useHostelUnitList,
} from "../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";

const HostelCreateApplicationStepWise = ({
  onClose,
  instituteId,
  hid,
  onParentClose,
  onRefetch,
  isEdit,
  onEdit,
  applicationDetail,
}) => {
  const { t } = useTranslation();
  const [step, setStep] = useState(0);
  const [state, setState] = useState({
    applicationName: "",
    gr_initials: "",
    hostelUnit: "",
    batch: "",
    classMaster: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");

  const [amissionCreateApplication] = useAdmissionCreateApplication();

  useEffect(() => {
    if (isEdit && applicationDetail) {
      setState((prev) => ({
        ...prev,
        applicationName: applicationDetail?.applicationName ?? "",
        gr_initials: applicationDetail?.gr_initials ?? "",
        department: applicationDetail?.applicationDepartment ?? "",
        batch: applicationDetail?.applicationBatch ?? "",
        hostelUnit: applicationDetail?.applicationUnit ?? "",
        classMaster: applicationDetail?.applicationMaster ?? "",
      }));
    }
  }, [isEdit, applicationDetail]);

  const { hostelBatchList } = useHostelBatchList({
    data: {
      hid: hid,
      page: 1,
      limit: 1000,
      search: "",
      id: instituteId,
    },
    skip: !hid ? !hid : !instituteId,
  });

  const { hostelStandardList } = useHostelStandardList({
    data: {
      hid: hid,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !hid,
  });

  const { hostelUnitList } = useHostelUnitList({
    data: {
      hid: hid,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !hid,
  });
  // console.info("applicationDetail", applicationDetail);
  const onSelectUnit = useCallback((value) => {
    setState((prev) => ({
      ...prev,
      hostelUnit: value,
    }));
  }, []);
  const onSelectBatch = useCallback((value) => {
    setState((prev) => ({
      ...prev,
      batch: value,
    }));
  }, []);

  const onSelectStandard = useCallback((value) => {
    setState((prev) => ({
      ...prev,
      classMaster: value,
    }));
  }, []);
  const onInputChange = (e) => {
    if (e.target.name === "gr_initials") {
      if (e.target.value?.length <= 10)
        setState((prev) => ({
          ...prev,
          [e.target.name]: e.target.value,
        }));
    } else
      setState((prev) => ({
        ...prev,
        [e.target.name]: e.target.value,
      }));
  };

  const onNext = () => {
    let validation = requiredField(state, ["gr_initials"]);
    if (existingRequiredField(validation)) {
      setStep(1);
    } else {
      setErrorField(validation);
    }
  };

  const onCreateApplication = (otherargs) => {
    if (isEdit) {
      onEdit({
        application_type: "Step Wise Admission",
        applicationName: state.applicationName,
        gr_initials: state.gr_initials,
        applicationBatch: state.batch?._id,
        applicationUnit: state.hostelUnit?._id,
        applicationMaster: state.classMaster?._id,
        ...otherargs,
      });
    } else {
      if (hid) {
        setDisabled((pre) => !pre);
        amissionCreateApplication({
          aid: hid,
          application: {
            flow: "HOSTEL",
            application_type: "Step Wise Admission",
            applicationName: state.applicationName,
            gr_initials: state.gr_initials,
            applicationUnit: state.hostelUnit?._id,
            applicationBatch: state.batch?._id,
            applicationMaster: state.classMaster?._id,
            ...otherargs,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onParentClose();
          })
          .catch({});
      }
    }
  };

  // console.info("smdfnj", state);
  return (
    <>
      {step === 0 ? (
        <Modal>
          <div className={styleIncome.modal_container}>
            <UserMemberContentWrapper>
              <div className={styleIncome.modal_container_outer_header}>
                <div className={styleIncome.modal_container_header}>
                  <h6>{t("create_new_application")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
                marginBottom: "1rem",
              }}
            >
              <FormInput
                label={t("application_name")}
                placeholder={t("please_enter_application_name")}
                name="applicationName"
                value={state.applicationName}
                type="text"
                onChange={onInputChange}
                errorShow={errorField["applicationName"]}
              />

              <FormInput
                label={t("application_initials")}
                placeholder={t("department_initials_placeholder")}
                name="gr_initials"
                value={state.gr_initials}
                type="text"
                onChange={onInputChange}
              />
              <CustomSelectUnit
                selectLabel={t("select_unit")}
                selectedValue={t("select_unit_placeholder")}
                options={
                  hostelUnitList?.list?.length > 0 ? hostelUnitList?.list : []
                }
                onClick={onSelectUnit}
                viewAs="NORMAL_FILTER"
                defalutValue={state.hostelUnit?.hostel_unit_name}
                errorShow={errorField["hostelUnit"]}
                isFull
                isDenied={isEdit}
              />
              <CustomSelectBatch
                selectLabel={t("select_batch")}
                selectedValue={t("select_batch_placeholder")}
                options={
                  hostelBatchList?.list?.length > 0 ? hostelBatchList?.list : []
                }
                onClick={onSelectBatch}
                viewAs="NORMAL_FILTER"
                defalutValue={state.batch?.batchName}
                errorShow={errorField["batch"]}
                isFull
                isDenied={isEdit}
              />
              <CustomSelectStandard
                selectLabel={t("select_standard")}
                selectedValue={t("please_select_the_standard_master")}
                defalutValue={state.classMaster?.className}
                onClick={onSelectStandard}
                name="class_master"
                options={
                  hostelStandardList?.list?.length > 0
                    ? hostelStandardList?.list
                    : []
                }
                errorShow={errorField["classMaster"]}
                viewAs="NORMAL_FILTER"
                isFull
                isDenied={isEdit}
              />
              <CreateButton label="next" onAction={onNext} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : (
        <HostelCreateApplicationOtherInfo
          applicationType="Normal Application"
          onClose={() => setStep(0)}
          onCreateApplication={onCreateApplication}
          applicationDetail={applicationDetail}
          isEdit={isEdit}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default HostelCreateApplicationStepWise;
