import { useCallback, useEffect, useState } from "react";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  assestsNavbarUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useAdmissionOngoingDirectApplicationExcel } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import HostelOngoingAdmissionCard from "./HostelOngoingAdmissionCard";
import HostelCreateApplicationConfig from "./HostelCreateApplicationConfig";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import CreateButton from "../../../Ui/Button/CreateButton";
import UploadExcelButton from "../../../Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import HostelOngoingAdmissionTotalCard from "./HostelOngoingAdmissionTotalCard";
import { useHostelOngoinApplicationList } from "../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";

const HostelOngoingAdmission = ({ hid, instituteId, fid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchCopy, setSearchCopy] = useState("");
  const [add, setAdd] = useState(false);
  const [fileUpload] = useFileUpload();
  const [disabled, setDisabled] = useState(false);
  const [applicationList, setApplicationList] = useState([]);

  const [admissionOngoingDirectApplicationExcel] =
    useAdmissionOngoingDirectApplicationExcel();
  const {
    hostelOngoinApplicationList,
    hostelOngoinApplicationListLoading,
    hostelOngoinApplicationListRefetch,
  } = useHostelOngoinApplicationList({
    data: {
      hid: hid,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !hid,
  });

  useEffect(() => {
    if (hostelOngoinApplicationList?.list?.length) {
      setApplicationList(hostelOngoinApplicationList?.list);
    }
  }, [hostelOngoinApplicationList?.list]);

  const onSearchEvent = (e) => {
    if (
      !hostelOngoinApplicationListLoading &&
      hostelOngoinApplicationList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      let ft = getApplicationFilter(
        hostelOngoinApplicationList?.list,
        searchCopy
      );
      setApplicationList(ft);
    }
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onRefetch = () => {
    hostelOngoinApplicationListRefetch();
  };

  const onNavigate = useCallback(() => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/admission`
        : `/q/${params.username}/member/staff/admission`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  const onDirectApplicationExcel = (e) => {
    if (e.target?.files?.length && hid) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            admissionOngoingDirectApplicationExcel({
              hid: hid,
              bodyParameter: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  // console.info("hostelOngoinApplicationList", getQuery);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.5rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("hostel_ongoing_applications")}</h6>
            </div>

            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    style={{
                      minWidth: "220px",
                    }}
                    onKeyUp={onCallBindSearch}
                    value={searchCopy}
                  />
                </div>
              </section>
              {/* {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : ( */}
              {getQuery.state?.role ? null : (
                <>
                  {/* <UploadExcelButton
                    onExcelFileUpload={onDirectApplicationExcel}
                    dynamicKey="direct_application"
                    title="Direct Application Excel"
                    label="Direct Application Excel"
                  /> */}
                  <CreateButton
                    label="create_new_application"
                    customStyle={{
                      paddingBlock: "0.4rem",
                      margin: "0",
                    }}
                    onAction={onToggle}
                  />
                </>
              )}
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
            position: "relative",
          }}
        >
          <HostelOngoingAdmissionTotalCard hid={hid} />
          {getQuery.state?.authorizedApplication?.length > 0
            ? applicationList?.map((application) =>
                getQuery.state?.authorizedApplication?.includes(
                  application?._id
                ) ? (
                  <HostelOngoingAdmissionCard
                    application={application}
                    key={application?._id}
                    hid={hid}
                    instituteId={instituteId}
                    onRefetch={onRefetch}
                  />
                ) : null
              )
            : applicationList?.map((application) => (
                <HostelOngoingAdmissionCard
                  application={application}
                  key={application?._id}
                  hid={hid}
                  instituteId={instituteId}
                  onRefetch={onRefetch}
                />
              ))}
          {hostelOngoinApplicationListLoading ? (
            searchCopy ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : searchCopy ? (
            hostelOngoinApplicationList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No ongoing application found related this search."
              />
            )
          ) : (
            hostelOngoinApplicationList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No application created."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <HostelCreateApplicationConfig
          onClose={onToggle}
          onRefetch={onRefetch}
          hid={hid}
          instituteId={instituteId}
          fid={fid}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default HostelOngoingAdmission;

const getApplicationFilter = (list, args) => {
  if (list?.length > 0) {
    return list?.filter((val) => {
      if (val?.applicationName?.toLowerCase()?.includes(args?.toLowerCase())) {
        return val;
      } else {
        return null;
      }
    });
  } else {
    return [];
  }
};
